<template>
  <div class="transfer-wrap">
    <el-transfer v-model="selected" :data="data" :props="{key:valueKey,label:label}" :render-content="renderFunc" @change="handleChange" :right-default-checked="rightCheck" @right-check-change="rightCheckChange" :titles="titles" :button-texts="['撤销','新增']">
      <template v-if="isEditScale" slot="right-footer">
        <el-button class="transfer-footer mar-lr-5" size="mini" :disabled="rightCheck.length === 0" @click="resetData">重置数据项</el-button>
        <small class="pull-right pad-10">*右侧显示计算比例</small>
      </template>
      <p slot="left-footer"></p>
    </el-transfer>
    <el-form v-if="isEditScale" v-show="isSelectedRight" :inline="true" ref="transform" :model="transferForm" class="transfer-fm" :rules="fmRules">
      <el-form-item label="计算类型" prop="calculateType">
        <el-radio-group v-model="transferForm.calculateType">
          <el-radio :label="1">+</el-radio>
          <el-radio :label="-1">-</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="比例" prop="scale">
        <el-input v-model="transferForm.scale"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button @click="changeScale">确定</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { deepJSONCopy } from "../../../util/util";
export default {
  props: {
    value: {
      type: Array
    },
    subitemId: {
      type: [Number, String]
    },
    data: {
      type: Array,
      default() {
        return [];
      }
    },
    label: {
      type: String,
      default: "name"
    },
    subitemAttr: {
      type: String
    },
    valueKey: {
      type: String,
      default: "id"
    },
    checked: {
      type: String,
      default: "checked"
    },
    titles: {
      type: Array,
      default() {
        return ["未关联表计", "已关联表计"];
      }
    },
    isEditScale: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      rightCheck: [], // 记录右侧选中项，在向左移动时会被删除，向右移动会被新增
      selected: [],
      dataToObj: {},
      moved: [], // 记录上次移到右边的值
      isClickShow: false,
      transferForm: {
        calculateType: 1,
        scale: "1"
      },
      fmRules: this.eachValidate({
        calculateType: {
          required: { required: true, trigger: "change", message: "必选" }
        },
        scale: { min: 0, required: { required: true, number: true } }
      })
    };
  },
  watch: {
    data() {
      let obj = {};
      let selected = [];
      let selectedObj = [];
      this.$each(this.data, item => {
        obj[item[this.valueKey]] = item;
        if (item[this.checked] === 1) {
          selected.push(item[this.valueKey]);
          selectedObj.push(item);
        }
      });
      this.selected = selected;
      this.dataToObj = obj;
      this.rightCheck = [];
      this.$emit("defaultMeter", deepJSONCopy(selectedObj));
      this.$emit("input", selectedObj);
    }
  },
  computed: {
    isSelectedRight() {
      if (this.rightCheck.length === 0) {
        this.isClickShow = false;
      }
      return this.isClickShow && this.rightCheck.length > 0;
    }
  },
  methods: {
    // 自定义文字
    renderFunc(h, option) {
      let childList = [option[this.label]];
      if (option.scale && option.calculateType) {
        childList.push(
          h("el-button", {
            class: { "pull-right": true, "mar-lr-5": true, "trans-btn": true },
            props: {
              type: option.calculateType > 0 ? "success" : "warning",
              size: "mini",
              round: true
            },
            domProps: {
              innerHTML: (option.calculateType > 0 ? "+" : "-") + option.scale
            }
          })
        );
      }
      return h("span", childList);
    },
    handleChange(value, direction, movedKeys) {
      if (direction === "right") {
        this.isClickShow = true;
        this.rightCheck = this.rightCheck.concat(movedKeys);
        if (this.isEditScale) {
          this.$nextTick(() => {
            // this.$refs.transform.resetFields();
            this.transferForm = {
              calculateType: 1,
              scale: "1"
            };
          });
        }
        this.moved = movedKeys;
      } else {
        this.rightCheck = [];
      }
      let selectedObj = [];
      this.$each(this.selected, item => {
        this.dataToObj[item][this.subitemAttr] = this.subitemId;
        selectedObj.push(this.dataToObj[item]);
      });
      this.$emit("input", selectedObj);
    },
    changeScale() {
      this.$refs.transform.validate(valid => {
        if (valid) {
          this.$each(this.moved, item => {
            this.dataToObj[
              item
            ].calculateType = this.transferForm.calculateType;
            this.dataToObj[item].scale = this.transferForm.scale;
            this.isClickShow = false;
          });
        }
      });
    },
    resetData() {
      this.isClickShow = true;
      this.$nextTick(() => {
        // this.$refs.transform.resetFields();
        this.transferForm = {
          calculateType: 1,
          scale: "1"
        };
      });
    },
    rightCheckChange(values, clickKey, c) {
      this.moved = values;
      this.rightCheck = values;
    }
  }
};
</script>

<style scoped>
.transfer-wrap {
  position: relative;
}
.transfer-fm {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.9);
  z-index: 1;
  display: flex;
  align-items: center;
  border: 1px solid #eee;
  padding: 18px;
  box-shadow: 2px 1px 2px 2px #ddd;
}
</style>



