<template>
  <div>
    <da-manage-page>
      <div slot="button">
        <el-button icon="my-zengjia" @click="dialogVisible=true;">新增</el-button>
      </div>
      <el-form slot="search" :inline="true" :model="searchForm.formdata">
        <el-form-item label="分项名称">
          <el-input v-model="searchForm.formdata.name"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="getTbList">查询</el-button>
        </el-form-item>
      </el-form>
      <da-table ref="table" slot="content" :colums="colsData" :table-list="tableList" :search-data="searchForm" @callBack="getTbList" :showSelection="false">

        <el-table-column property="operation" label="操作" width="200" slot="operation" align="center" fixed="right">
          <template slot-scope="scope">
            <el-button size="small" @click="editTb(scope.row)">编辑</el-button>
            <el-button size="small" @click="getTransfer('energySubitemMeter',scope.row)">关联表计</el-button>
            <el-button size="small" @click="delTb(scope.row)">删除</el-button>
          </template>
        </el-table-column>

      </da-table>
    </da-manage-page>

    <da-dialog :title="title" :visible.sync="dialogVisible" size="450px" @close="closeDialog('addForm')">
      <el-form label-width="90px" :model="addForm" :rules="rules" ref="addForm">
        <el-form-item label="分项名称" prop="name">
          <el-input auto-complete="off" v-model="addForm.name"></el-input>
        </el-form-item>
        <el-form-item label="顺序" prop="showSort">
          <el-input auto-complete="off" v-model="addForm.showSort"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveDialog('addForm')">确 定</el-button>
        <el-button @click="dialogVisible = false">取 消</el-button>
      </div>
    </da-dialog>
    <!-- 关联表计 -->
    <da-dialog title="关联表计" :visible.sync="energySubitemMeterDialogVisible" size="650px">
      <relev-transfer ref="energySubitemMeter" :visible.sync="energySubitemMeterDialogVisible" :params-attr="energySubitemId" :base-attr="meterAttr"></relev-transfer>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveTransferDialog('energySubitemMeter')">确 定</el-button>
        <el-button @click="energySubitemMeterDialogVisible = false">取 消</el-button>
      </div>
    </da-dialog>
  </div>
</template>
<script>
import { difference } from "../../../util/util";
import relevTransfer from "./template/relevProduct";
export default {
  name: "energySubitem",
  components: { relevTransfer },
  data() {
    return {
      // 查询表单
      searchForm: {
        formdata: { name: "" },
        currentPage: 1,
        pageSize: 20
      },
      tableList: {}, // 数据列表
      colsData: [
        // 表头
        { label: "分项名称", prop: "name", align: "left", sortable: true },
        { label: "顺序", prop: "showSort", align: "left", sortable: true }
      ],
      dialogVisible: false,
      addForm: { name: "", showSort: "" },
      rules: this.eachValidate({
        name: { required: true, maxString: "40" },
        showSort: {}
      }),
      energySubitemId: "",
      // 关联表计
      energySubitemMeterDialogVisible: false,
      meterAttr: {
        dataUrl: "rest/energySubitem/energySubitemMeter", // 获取数据的地址
        getPramsAttr: "energySubitemId", // 获取数据的参数属性名
        saveUrl: "rest/energySubitem/energySubitemMeter", // 保存数据的地址
        valueKey: "meterId",
        label: "meterName",
        titles: ["未关联表计", "已关联表计"],
        isEditScale: true
      }
    };
  },
  computed: {
    title() {
      //弹窗标题
      return this.addForm.id ? "编辑分项资料管理" : "新增分项资料管理";
    },
    urlType() {
      //URL请求方式
      return this.addForm.id ? "put" : "post";
    }
  },
  watch: {},
  methods: {
    getTbList() {
      let formName = "table";
      this.$ajax
        .get("rest/energySubitem/page", {
          params: this.searchForm,
          loadingTarget: this.$refs[formName]
        })
        .then(res => {
          if (res.data.status) {
            this.tableList = res.data;
          }
        });
    },
    editTb(row) {
      this.dialogVisible = true;
      this.addForm = JSON.parse(JSON.stringify(row));
    },
    closeDialog(formName) {
      this.$nextTick(function() {
        this.$refs[formName].resetFields();
        this.addForm = { name: "", showSort: "" };
      });
    },
    saveDialog(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$ajax[this.urlType]("rest/energySubitem", this.addForm).then(
            res => {
              if (res.data.status) {
                this.$message.success(res.data.answerMsg);
                this.dialogVisible = false;
                this.getTbList();
              } else {
                this.$message.error(res.data.answerMsg);
              }
            }
          );
        }
      });
    },
    delTb(row) {
      this.$confirm("是否删除该记录?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$ajax
          .delete("rest/energySubitem", { params: { id: row.id } })
          .then(res => {
            if (res.data.status) {
              this.$message.success(res.data.answerMsg);
              this.getTbList();
            } else {
              this.$message.error(res.data.answerMsg);
            }
          });
      });
    },
    getTransfer(refName, row) {
      this.energySubitemId = row.id;
      this[refName + "DialogVisible"] = true;
      this.$nextTick(() => {
        this.$refs[refName].getData(row);
      });
    },
    saveTransferDialog(refName) {
      this.$refs[refName].saveData();
    }
  },
  mounted() {
    this.getTbList();
  }
};
</script>



