<template>
  <div>
    <es-transfer v-model="selectedLists" :data="dataLists" :is-edit-scale="baseAttr.isEditScale" :titles="baseAttr.titles" :subitem-id="paramsAttr" :subitem-attr="baseAttr.getPramsAttr" @defaultMeter="defaultMeter" :value-key="baseAttr.valueKey" :label="baseAttr.label"></es-transfer>
  </div>
</template>

<script>
import esTransfer from "../../template/transform";
import { difference } from "@/util/util.js";
export default {
  components: {
    esTransfer
  },
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    // 必填字段,不会变的值
    baseAttr: {
      type: Object,
      default() {
        return {
          dataUrl: "rest/enterpriseOrg/orgMeter", // 获取数据的地址
          getPramsAttr: "orgId", // 获取数据的参数属性名
          saveUrl: "rest/enterpriseOrg/orgMeter", // 保存数据的地址
          valueKey: "meterId",
          label: "meterName",
          titles: ["未关联表计", "已关联表计"],
          isEditScale: true
        };
      }
    },
    // 必填，会变
    paramsAttr: {
      type: [String, Number],
      default: "382"
    }
  },
  data() {
    return {
      defaultSelectLists: [], // 计算时初始化选中的数据，不会改变，用作保存时对比操作后的结果
      selectedLists: [], // 选中数据,初始化时不需要设值，会根据dataLists里的checked属性自动过滤
      dataLists: [] // 所有数据
    };
  },
  methods: {
    // 关联表计
    getData(row) {
      let params = {};
      params[this.baseAttr.getPramsAttr] = this.paramsAttr;
      this.$ajax
        .get(this.baseAttr.dataUrl, {
          params: params
        })
        .then(res => {
          if (res.data.status) {
            this.dataLists = res.data.records;
          }
        });
    },
    defaultMeter(data) {
      this.defaultSelectLists = data;
    },
    saveData() {
      if (
        this.selectedLists.some(item => {
          return item.calculateType === null;
        })
      ) {
        this.$message.error("尚有关联的表计未定义比例，请填写或重置该表计。");
        return false;
      }
      let editLists = [];
      let key = this.baseAttr.valueKey;
      let newLists = difference(
        this.defaultSelectLists,
        this.selectedLists,
        key,
        // 遍历总数据（初始+修改的集合），从中获取获取编辑的对象
        (item, orgArr) => {
          // 当前对象在初始数据中&& 存在修改的数据中&&不在已存入的编辑列表（区分初始数据和修改数据）
          if (
            this.defaultSelectLists.some(value => item[key] === value[key]) &&
            this.selectedLists.some(value => item[key] === value[key]) &&
            !editLists.some(value => item[key] === value[key])
          ) {
            editLists.push(item);
          }
          return orgArr.some(value => item[key] === value[key]);
        }
      );
      this.$ajax
        .post(this.baseAttr.saveUrl, {
          saveList: newLists.saveList,
          updateList: editLists,
          deleteList: newLists.deleteList
        })
        .then(res => {
          this.$emit("update:visible", false);
        });
    }
  },
  mounted() {}
};
</script>



